import { useEffect, useState } from 'react';


function MyComponent() {
    const [error, setError] = useState(null);
    const [item, setItem] = useState('');
    const [itemArr, setArr] = useState([]);

    // Note: the empty deps array [] means
    // this useEffect will run once
    // similar to componentDidMount()


    if (item === '') {
        fetch(`http://192.168.0.7:3000/Stocks/${sessionStorage.getItem('username')}`, {
            "method": "GET",
        })
            .then(res => res.json())
            .then(
                (result) => {

                    try {

                        setItem(result.stockArr[Math.floor(Math.random() * result.stockArr.length)].ticker);

                        
                    } catch {
                        console.log("No tickers")
                    }


                    return;
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }

    if (item === '') {
        console.log('fail');
        return null;
    }

   

    if (itemArr.length === 0 && sessionStorage.getItem('reqSent') !== 'true') {

    

        sessionStorage.setItem('reqSent', 'true');

        fetch(`https://yahoo-finance15.p.rapidapi.com/api/yahoo/qu/quote/${item}/asset-profile`, {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "yahoo-finance15.p.rapidapi.com",
		        "x-rapidapi-key": "d0a078cdcfmsh1ebd346e137e253p166073jsn586625af6c35"
            }
        })
            .then(res => res.json())
            .then(
                (result) => {


                    if(result.assetProfile === undefined || result.assetProfile === null){
                        sessionStorage.setItem('reqSent', 'false');
                        return;
                    }

                    let info = [];
               

                    info.push(item);
                    info.push(result.assetProfile.city);
                    info.push(result.assetProfile.industry);
                    info.push(result.assetProfile.fullTimeEmployees);
                    info.push(result.assetProfile.website);
                    info.push(result.assetProfile.companyOfficers[0].name);

                    setArr(info);

                    sessionStorage.setItem('reqSent', false);


                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    } else if (itemArr.length > 0) {
        return (

            <div className = "SotDEle">
                <p>Company symbol: &nbsp;&nbsp;{itemArr[0]}</p> 
                <p>Based in: &nbsp;&nbsp;{itemArr[1]}</p>
                <p>Industry: &nbsp;&nbsp;{itemArr[2]}</p>
                <p>Employee Count: &nbsp;&nbsp;{itemArr[3]}</p>
                <p>Website: &nbsp;&nbsp;{itemArr[4]}</p> 
                <p>CEO: &nbsp;&nbsp;{itemArr[5]}</p> 
            </div>
        )

    }

    return (
        <div>
            <p>Loading...</p>

            <p>If stock doesn't load, ticker may have changed. Refresh for a new one.</p>
        </div>
    )

}




export default MyComponent;

